<template>
  <div>
    <v-snackbar v-model="snackbar.show" :timeout="4000" top :color="snackbar.color">
      <span>{{snackbar.text}}</span>
      <v-btn color="white" flat @click="snackbar.show = false">Close</v-btn>
    </v-snackbar>

    <v-navigation-drawer dark disable-resize-watcher app v-model="drawer" class="secondary">
      <v-list>
        <v-list-tile v-for="(link, index) in links" :key="index" :to="link.route" :name="link.text">
          <v-list-tile-action>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{link.text}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar scroll-off-screen dark flat class="secondary">
      <v-toolbar-side-icon class="hidden-sm-and-up" @click="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-items v-for="link in links" :key="link.name" class="hidden-xs-only">
        <v-btn flat :to="link.route" :name="link.text">
          <v-icon left>{{link.icon}}</v-icon>
          <span>{{link.text}}</span>
        </v-btn>
      </v-toolbar-items>
      <v-spacer class="hidden-sm-and-up"></v-spacer>
      <v-toolbar-items>
        <v-btn flat @click="openContact">
          <v-icon left>mail</v-icon>
          <span>Contact</span>
        </v-btn>
      </v-toolbar-items>
      <v-spacer class="hidden-sm-and-up"></v-spacer>
    </v-toolbar>
    <Contact @input="handleDialog" :dialog="dialog" @handleSnackBar="handleSnackBar"/>
  </div>
</template>

<script>
import Contact from "./Contact.vue";
export default {
  components: {
    Contact
  },
  data() {
    return {
      links: [
        { icon: "home", text: "Home", route: "/" },
        { icon: "folder", text: "My projects", route: "/portfolio" },
        { icon: "person", text: "About me", route: "/about" }
      ],
      dialog: false,
      drawer: false,
      snackbar: {
        show: false,
        color: "",
        message: ""
      }
    };
  },
  methods: {
    handleSnackBar(snack) {
      this.snackbar.show = snack.show;
      this.snackbar.color = snack.color;
      this.snackbar.text = snack.text;
    },
    handleDialog(value) {
      this.dialog = value;
    },
    openContact() {
      this.dialog = true;
    },
    toggleDrawer() {
      this.drawer = !this.drawer;
    }
  }
};
</script>

