<template>
  <div>
    <v-dialog v-model="show" max-width="800px" max-height="1000px">
      <v-container class="white">
        <v-layout row justify-center>
          <v-flex>
            <h2>Contact me</h2>
            <form>
              <v-text-field
                v-validate="'required'"
                v-model="contact.name"
                :error-messages="errors.collect('name')"
                label="Name"
                data-vv-name="name"
                required
              ></v-text-field>
              <v-text-field
                v-validate="'required|email'"
                v-model="contact.email"
                :error-messages="errors.collect('email')"
                label="Email"
                data-vv-name="email"
                required
              ></v-text-field>
              <v-textarea
                class="mb-2"
                v-validate="'required'"
                v-model="contact.content"
                :error-messages="errors.collect('content')"
                label="Message"
                data-vv-name="content"
                required
              ></v-textarea>
              <v-layout>
                <v-btn @click="submit">Send</v-btn>
                <v-spacer></v-spacer>
                <v-btn @click="close">Close</v-btn>
              </v-layout>
            </form>
          </v-flex>
        </v-layout>
      </v-container>
    </v-dialog>
  </div>
</template>

<script>
import Vue from "vue";
import VeeValidate from "vee-validate";

Vue.use(VeeValidate);

export default {
  props: {
    dialog: false
  },
  $_veeValidate: {
    validator: "new"
  },
  data: () => ({
    contact: {
      name: "",
      email: "",
      content: ""
    },
    dictionary: {
      attributes: {
        email: "",
        content: "",
        name: ""
      }
    }
  }),
  mounted() {
    this.$validator.localize("en", this.dictionary);
  },

  methods: {
    submit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.axios
            .post("https://dgapp.club/api/contact", this.contact)
            .then(() => {
              const snackbar = {};
              snackbar.show = true;
              snackbar.color = "success";
              snackbar.text = "Message was sent succesfully";
              this.$emit("handleSnackBar", snackbar);
              this.close();
            })
            .catch(error => {
              const snackbar = {};
              snackbar.show = true;
              snackbar.color = "error";
              snackbar.text = error.message;
              this.$emit("handleSnackBar", snackbar);
            });
        }
      });
    },
    clear() {
      Object.keys(this.contact).forEach(key => {
        this.contact[key] = "";
      });
      this.$validator.reset();
    },
    close() {
      this.clear();
      this.show = false;
    }
  },
  computed: {
    show: {
      get() {
        return this.dialog;
      },
      set(dialog) {
        this.$emit("input", dialog);
      }
    }
  }
};
</script>

<style scoped>
.contact-dialog {
  background-color: white;
}
.v-text-field .v-label--active {
  color: blue !important;
}
</style>
