import Vue from "vue";
import Router from "vue-router";
import Portfolio from "./views/Portfolio";
import About from "./views/About";
import Blog from "./views/Blog";
import Home from "./views/Home";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/blog",
      name: "blog",
      component: Blog
    },
    {
      path: "/portfolio",
      name: "portfolio",
      component: Portfolio
    },
    {
      path: "*",
      redirect: "/"
    }
  ]
});
