<template>
  <div>
    <h2 class="mt-4 ml-4 grey--text text--darken-2">Blog</h2>
    <v-container fluid>
      <v-layout row wrap>
        <v-flex d-flex xs12>
          <v-layout column>
            <v-flex flat v-for="(blog, index) in sortedBlogs" :key="index" class="mb-4">
              <v-card flat>
                <v-card-title>
                  <h5 class="headline">{{blog.title}}</h5>
                  <v-spacer></v-spacer>
                  <span class="grey--text text--darken-1">{{blog.date}}</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>{{blog.content}}</v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blogs: [
        {
          title: "A blog post",
          content:
            "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Cum eos quidem dolor! Nemo, vero aperiam ut cupiditate praesentium deleniti, error a veritatis accusantium voluptatum voluptas qui incidunt voluptates dolor itaque.",
          date: "03.10.2005"
        },
        {
          title: "This is a blog post",
          content:
            "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Possimus necessitatibus odit sed, soluta nam cumque delectus facilis similique officia nostrum blanditiis. Fugiat explicabo rem cumque quam quia nemo maiores quae!",
          date: "05.01.1996"
        },
        {
          title: "Random blog post",
          content:
            "  Lorem ipsum dolor, sit amet consectetur adipisicing elit. Deserunt facilis quod illum qui vel enim eveniet, perferendis vitae temporibus. Enim exercitationem sed labore repudiandae aut? Doloremque ex placeat ab illum.",
          date: "02.10.2010"
        }
      ]
    };
  },
  computed: {
    sortedBlogs: function() {
      return this.blogs
        .slice()
        .sort(
          (a, b) =>
            this.$moment(a.date, "DD.MM.YYYY") -
            this.$moment(b.date, "DD.MM.YYYY")
        );
    }
  }
};
</script>
